/* Settings */
/* Base */
@import-normalize;
html {
  height: 100%;
  font-size: 62.5%;
  font-size: calc(1em * 0.625);
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 100%;
  font-size: 1.8rem;
  line-height: 1.5;
  color: #798088;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  max-width: 100%;
  vertical-align: top;
}

strong,
b {
  font-weight: 600;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}

button,
select,
input,
textarea {
  font: inherit;
}

button,
a {
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

a {
  text-decoration: none;
  color: #0088FE;
}

