/** ======================================
 * Atom : Wrapper
 * ======================================= */
.l-wrapper {
  display: block;
  margin: 0 auto;
  max-width: 140rem;
  padding-left: 2rem;
  padding-right: 2rem;
}

@media only screen and (min-width: 48em) {
  .l-wrapper {
    padding-left: 3rem;
    padding-right: 3rem;
  }
}

.l-wrapper--S {
  max-width: 120rem;
}

.l-wrapper--L {
  max-width: 168rem;
}

/* Atoms */
.a-button {
  display: inline-block;
  background-color: #29E4AE;
  text-align: center;
  color: #FFF;
  border: 0;
  min-height: 5.2rem;
  min-width: 5.2rem;
  text-decoration: none;
  border-radius: 999rem;
  font-family: "Ubuntu", sans-serif;
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 1.1;
  cursor: pointer;
  padding: 1rem 2.2rem;
  outline: 0;
  box-shadow: 0 0.8rem 4rem -0.8rem rgba(0, 0, 0, 0.4);
  transition: all .2s ease-out;
}

.a-button:hover, .a-button:focus {
  transform: scale(1.1);
  background-color: #56eabf;
  box-shadow: 0 0.8rem 3rem -0.8rem rgba(0, 0, 0, 0.6);
}

.a-button:disabled, .a-button.is-disabled {
  color: #798088;
}

.a-button > * + .a-icon {
  margin-left: .8rem;
}

.a-button > .a-icon + * {
  margin-left: .8rem;
}

.a-button--circle {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 1.8rem;
}

.a-button--slap {
  position: relative;
  display: inline-flex;
  align-items: center;
  background: #EEF4FB;
  color: #333;
  font-size: 2.2rem;
}

.a-button--slap::after {
  content: attr(data-counter);
  margin-left: 1.4rem;
}

.a-button--slap:hover, .a-button--slap:focus {
  transform: scale(1.1);
  background: #f4f7fb;
}

.a-button--slap.is-slapping {
  animation: slap .2s ease-in-out;
}

@keyframes slap {
  0% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1.1);
  }
}

.a-icon {
  display: inline-block;
  vertical-align: middle;
}

.a-icon svg {
  fill: currentColor;
  display: block;
  height: 2.4rem;
  width: 2.4rem;
}

.a-icon--inline {
  display: inline-flex;
  align-items: center;
  height: 1em;
  line-height: 1;
}

.a-icon--inline svg {
  margin-top: -0.6em;
  margin-bottom: -0.4em;
}

.a-icon--inline:not(:first-child),
.a-icon--inline + * {
  margin-left: .8rem;
}

.a-input {
  border: 0;
  border-radius: 999rem;
  box-shadow: 0 0.8rem 4rem -0.8rem rgba(0, 0, 0, 0.4);
  width: 42rem;
  max-width: 100%;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  padding: 1.4rem 2rem 1.7rem;
  outline: 0;
  transition: all .2s ease-out;
  line-height: 1;
}

.a-input::placeholder {
  color: #A2A8B7;
}

.a-input:hover, .a-input:focus {
  box-shadow: 0 0.8rem 2rem -0.8rem rgba(0, 0, 0, 0.6);
}

