  }
}

@media only screen and (min-width: 64em) {
  .u-hidden-L {
    display: none !important;
  }
  .u-visible-L {
    display: block !important;
  }
}

.u-visuallyHidden {
  position: absolute !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px;
  padding: 0 !important;
  border: 0 !important;
}
